<template>
  <div class="login-wrap" id="app" style="display: none;" :style="{display: 'block'}">
    <div class="ms-login">
      <div class="ms-title">注册/登录</div>
      <el-form :model="param" :rules="rules" ref="login" label-width="0px" class="ms-content">
        <el-form-item prop="phone">
          <el-input v-model="param.phone" autofocus="true" placeholder="请输入您的手机号" maxlength="11">
            <!--<el-button slot="prepend" icon="el-icon-mobile-phone"></el-button>-->
          </el-input>
        </el-form-item>
        <el-form-item prop="code">
          <el-input v-model="param.code" placeholder="请输入短信验证码" @keyup.enter.native="submitForm()">
            <!--<el-button slot="prepend" icon="el-icon-lock"></el-button>-->
          </el-input>
          <el-button
            class="code-btn"
            type="primary"
            size="mini"
            round
            @click="getVCode()"
            :disabled="second!=60"
          >{{ vcode_text }}</el-button>
        </el-form-item>
        <div class="login-btn">
          <el-button type="primary" @click="submitForm()">登录</el-button>
        </div>
        <div class="rule mt5">
          点击“登录”即表示您已阅读并同意
          <a href="#" style="color: #24268d">《用户协议》</a>
        </div>
        <div class="other-login mt10">
          <div>第三方登录</div>
          <div class="login-tips mt5">
            <a href="#">
              <img class="icon" title="微信登录" src="../assets/img/login/weixin.png" />
            </a>
            <a href="#">
              <img class="icon" title="QQ登录" src="../assets/img/login/qq.png" />
            </a>
            <a href="#">
              <img class="icon" title="微博登录" src="../assets/img/login/weibo.png" />
            </a>
          </div>
        </div>
      </el-form>
    </div>
  </div>
</template>

<script>
import loginApi from "../network/api/login";

let timer;

export default {
  name: "login",
  data() {
    let validatePhone = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入手机号"));
      } else {
        const reg = /^[1][3|4|5|6|7|8|9][0-9]{9}$/;
        if (!reg.test(value)) {
          callback(new Error("手机号格式不正确"));
        } else {
          callback();
        }
      }
    };

    return {
      second: 60,
      vcode_text: "获取验证码",
      param: {
        phone: "",
        code: ""
      },
      rules: {
        phone: [{ validator: validatePhone, trigger: "blur" }],
        code: [{ required: true, message: "请输入验证码", trigger: "blur" }]
      }
    };
  },
  methods: {
    getVCode() {
      this.$refs.login.validateField("phone", valid => {
        if (!valid) {
          loginApi
            .sendCode(this.param.phone)
            .then(res => {
              this.$message.success("短信已发送");
              this.countDownTimer();
            })
            .catch(res => {
              this.$message.error(
                res.response && res.response.data && res.response.data.msg
                  ? res.response.data.msg
                  : "验证码获取失败"
              );
            });
        } else {
          this.$message.error(valid);
        }
      });
    },
    // 获取验证码
    countDownTimer() {
      timer = setInterval(() => {
        this.second -= 1;
        if (this.second > 0) {
          this.vcode_text = "重新发送(" + this.second + ")";
        } else {
          this.second = 60;
          clearInterval(timer);
          this.vcode_text = "获取验证码";
        }
      }, 1000);
    },
    submitForm() {
      this.$refs.login.validate(valid => {
        if (valid) {
          let data = this.param;

          loginApi
            .login(this.param.phone, this.param.code)
            .then(res => {
              console.error("登录信息===> ", res);
              this.$message.success("登录成功");
              let user = res.data.result;
              storage.set("user", user);
              this.$router.push("/");
            })
            .catch(res => {
              this.$message.error(
                res && res.data && res.data.msg ? res.data.msg : "登录失败"
              );
            });
          return true;
        } else {
          return false;
        }
      });
    },
    login_weixin() {},
    login_qq() {},
    login_weibo() {}
  }
};
</script>

